import axios from "axios"
import { axiosClient } from "@rp/react/axios-client"
import { useMutation } from "@tanstack/react-query"

type OnboardingPayload = {
  industry: string
  topics: Array<string>
  goals: Array<string>
  role: string
}

async function submitOnboardingResults(body: OnboardingPayload) {
  try {
    const { data } = await axiosClient.post<unknown>("/marketing/onboarding-survey", body)
    return data
  } catch (error) {
    if (!axios.isAxiosError(error)) {
      throw error
    }

    throw error
  }
}

function useOnboardingSubmit() {
  return useMutation({
    mutationFn: submitOnboardingResults,
  })
}

export { useOnboardingSubmit }
